<template>
  <div id="login">
    <div class="container" style="padding-top: 1.5rem;">
      <div style="margin-top: 30px;">
        <div class="row">
          <div class="col-md-6 offset-md-3">
            <h4>Login</h4>
            <hr />
            <form @submit.prevent="login" action autocomplete="off">
              <div v-if="error" class="alert alert-danger" role="alert">
                <strong>{{ error }}</strong>
              </div>
              <div class="form-group">
                <label for>Nome utente</label>
                <input v-model="user.username" type="text" class="form-control" />
              </div>
              <div class="form-group">
                <label for>Password</label>
                <input v-model="user.password" type="password" class="form-control" />
              </div>
              <div class="form-group">
                <button class="btn btn-primary float-right">Accedi</button>
              </div>
            </form>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      user: {
        username: "",
        password: ""
      },
      error: null
    };
  },
  methods: {
    login() {
      this.axios
        .post(this.apiURL + "/login", this.user)
        .then(r => {
          let token = btoa(
            JSON.stringify({
              user: r.data.user_id,
              key: r.data.api_key
            })
          );

          localStorage.setItem("AUTH_TOKEN", token); // Persisti login
          this.$router.push("/");
        })
        .catch(r => {
          this.error = r.response.data.message;
          this.$store.dispatch("logout");
        });
    }
  }
};
</script>

